import { PropsWithChildren } from "react"
import { BigdataChatAvatar } from "@rp/react/common/assets"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Box, SxProps, Typography, alpha } from "@rp/react/web/mui/core"

const ICON_SIZE = 40
const ICON_MARGIN = 16

const BriefStepsLegend = ({ children, sx }: PropsWithChildren<{ sx?: SxProps }>) => {
  const { isMobile } = useDeviceInfo()
  return (
    <Box
      sx={{
        mt: 1,
        display: isMobile ? "block" : "flex",
        gap: `${ICON_MARGIN}px`,
        mx: 0,
        alignItems: "center",
        ...sx,
      }}
    >
      <Box
        sx={{
          height: ICON_SIZE,
          mt: isMobile ? 2 : undefined,
          display: "flex",
          alignItems: "center",
          gap: 2,
        }}
      >
        <BigdataChatAvatar height={ICON_SIZE} width={ICON_SIZE} />{" "}
        {isMobile ? <Typography color={alpha("#ffffff", 0.7)}>Bigdata</Typography> : null}
      </Box>
      <Box sx={{ mt: isMobile ? 2 : undefined }}>{children}</Box>
    </Box>
  )
}

export { BriefStepsLegend }
